/*Navigation*/
.ant-affix {
  z-index: 1030;
  top: 50px !important;
}

.ant-breadcrumb {
  font-family: "Poppins", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Helvetica Neue", Arial, sans-serif;
  line-height: 1.5;
  color: #53535f;
  font-size: 13px;
  font-weight: 500;
}
.ant-breadcrumb a {
  color: #72849a;
}
.ant-breadcrumb a:hover {
  color: #3f87f5;
}

.ant-dropdown {
  color: #53535f;
  margin: 0;
  padding: 0;
  list-style: none;
  z-index: 1000;
}

.ant-dropdown-menu {
  min-width: 10rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
}
.ant-dropdown-menu ul {
  list-style: none;
  padding-left: 0px;
}

.ant-dropdown-menu-item,
.ant-dropdown-menu-submenu-title {
  padding: 0.5rem 1.25rem;
  margin: 0;
  font-size: 0.875rem;
  font-weight: 400;
  color: #53535f;
  line-height: 1.5;
}
.ant-dropdown-menu-item > a,
.ant-dropdown-menu-submenu-title > a {
  display: block;
  padding: 0;
  margin: 0;
  transition: all 0.3s;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -o-transition: all 0.3s;
  -ms-transition: all 0.3s;
  color: #53535f;
}
.ant-dropdown-menu-item:hover, .ant-dropdown-menu-item:focus,
.ant-dropdown-menu-submenu-title:hover,
.ant-dropdown-menu-submenu-title:focus {
  background-color: #fafafa;
  color: #2a2a2a;
}
.ant-dropdown-menu-item:hover > a, .ant-dropdown-menu-item:focus > a,
.ant-dropdown-menu-submenu-title:hover > a,
.ant-dropdown-menu-submenu-title:focus > a {
  color: #2a2a2a;
  background-color: transparent;
}

.ant-dropdown-menu-item-disabled,
.ant-dropdown-menu-submenu-title-disabled {
  color: rgba(83, 83, 95, 0.25);
  cursor: not-allowed;
}
.ant-dropdown-menu-item-disabled:hover,
.ant-dropdown-menu-submenu-title-disabled:hover {
  color: rgba(83, 83, 95, 0.25);
  background-color: #fff;
  cursor: not-allowed;
}

.ant-dropdown-menu-item-selected,
.ant-dropdown-menu-submenu-title-selected {
  background-color: #fafafa;
  color: #2a2a2a;
}
.ant-dropdown-menu-item-selected > a,
.ant-dropdown-menu-submenu-title-selected > a {
  background-color: #fafafa;
  color: #2a2a2a;
}

.ant-menu {
  font-size: 0.875rem;
  line-height: 1.5;
  font-weight: 500;
  color: rgba(42, 42, 42, 0.75);
}
.ant-menu:not(.ant-menu-horizontal) .ant-menu-item-selected {
  background: #3f87f5; /* The Fallback */
  background: rgba(63, 135, 245, 0.9);
  color: #fff;
}
.ant-menu:not(.ant-menu-horizontal) .ant-menu-item-selected a {
  color: #fff;
}
.ant-menu:not(.ant-menu-dark) a {
